.card-image {
    width: 100%;
    height: 180px;
}

.image-answer{
    cursor: pointer;
    height: 330px;
    width: 100%;
    overflow: hidden;
    border-radius: 10px;
    margin-bottom: 20px
}

.image-answer img{
    width: 100%;
    height: inherit;
}

.image-selected{
    border: 3px solid #36a6de;
}

.image-danger {
    border: 3px solid #d43232;
}