* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

.toggle-button {
    position: absolute;
    top: 1.2rem;
    right: 1rem;
    display: none;
}

.hambarger {
    display: block;
    width: 25px;
    height: 3px;
    margin-bottom: 3px;
    background-color: #45beff;
}

.btn-align {
    margin-top: 15px;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #f7faff;
    margin-bottom: 25px;
    padding: 5px;
}

.brand-title {
    font-size: 1.5rem;
    color: #454766;
    margin-left: 15%;
}

.nav-links {
    margin-right: 15%;
}

.top-list {
    display: flex;
    list-style: none;
    margin: 0;
}

    .top-list li {
        position: relative;
    }

.main-link {
    text-decoration: none;
    font-size: 1.1rem;
    color: #454766;
    padding: 1.3rem;
    display: block;
}

.bottom-list {
    position: absolute;
    display: none;
    flex-direction: column;
    list-style: none;
    top: 68px;
    left: 0px;
    padding: 0;
    margin: 0;
    z-index: 9;
    border-radius: 0 0 5px 5px;
}

.sub-link {
    text-decoration: none;
    color: #454766;
    padding: 1rem;
    display: block;
}

.main-link:hover {
    background-color: #cce9f0;
    border-radius: 5px;
}

.bottom-list li {
    background-color: #a0d0db;
}

.top-list li:hover ul,
.top-list li:active ul {
    display: flex;
}

.bottom-list li:hover {
    background-color: #71dbf5;
    border-radius: 5px;
}

@media (max-width: 420px) {

    .brand-title {
        margin-left: 0px;
    }

    .toggle-button {
        display: block;
    }

    .navbar {
        flex-direction: column;
        padding: 10px;
        align-items: flex-start;
    }

    .top-list {
        display: none;
        flex-direction: column;
    }

    .main-link {
        position: relative;
        text-decoration: none;
        font-size: 1.1rem;
        color: #454766;
        padding: .6rem;
        display: block;
        margin: 0;
    }

    .bottom-list {
        position: absolute;
        top: 0px;
        left: 67px;
        z-index: 10;
    }

    .top-list.active {
        display: flex;
    }

}
